body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  position: relative;
  text-align: center;
  min-height: 200px;
  max-height: 430px;
  min-width: 390px;
  max-width: 600px;
  background-color: #e4e4e4;
  overflow: hidden;
  margin:auto;
}

@media (max-width: 400px) {
  max-width: 400px;
  width: 400px;
}

.App-header {
  background-color: #e4e4e4;
  min-height: 50px;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  font-size: calc(9px + 2vmin);
  color: #04091e;
  padding-bottom: 20px;
  overflow: hidden;
  vertical-align: middle;
}

@media only screen and (max-width: 600px) {
  .App-header {
    background-color: lightblue;
    width:800px;
    overflow: scroll;
  }
}


.App-main {
  /* background-image: url("./header1-bg.png"); */
  background-color: #ffffff;
  background-size: 100%;
  background-repeat: no-repeat;
  height: 300px;
  overflow: hidden;
  display: flex;
  justify-content: center;
  align-items: center;
}

.App-footer {
  background-color: #e4e4e4;
  min-height: 50px;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  font-size: calc(9px + 2vmin);
  color: #f20253;
  overflow: hidden;
}

button {
  min-width: 40px;
  min-height: 40px;
  border-style: none;
  background: #f20253;
  Color: white;
  font-family: 'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif;
  /* margin-left:15px; */
}

button:disabled {
  background: #ce7a96;
}

button>span {
  min-width: 40px;
  min-height: 40px;
  font-weight: bold;
}

.hide {
  visibility: hidden;
}

.SubjectInformationForm {
  position: absolute;
  top: 50%;
  left: 50%;
  height: 30%;
  width: 50%;
  margin: -15% 0 0 -25%;
}



.DownloadForm {
  position: absolute;
  top: 50%;
  left: 50%;
  height: 30%;
  width: 50%;
  margin: -15% 0 0 -25%;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.noselect {
  -webkit-touch-callout: none;
  /* iOS Safari */
  -webkit-user-select: none;
  /* Safari */
  /* Konqueror HTML */
  -moz-user-select: none;
  /* Firefox */
  -ms-user-select: none;
  /* Internet Explorer/Edge */
  user-select: none;
  /* Non-prefixed version, currently
  supported by Chrome and Opera */
}

.nonBlocking {
  color: rgba(255, 255, 255, 0.15);
  font-size: 80px;
  position: fixed;
  top: 150px;
  left: 100px;
  -webkit-transform: rotate(45deg);
  transform: rotate(30deg);
  pointer-events: none;
  font-family: Impact, Charcoal, sans-serif;
}

.formContainer {
  /* position: absolute; */
  width: 70%;
  display: flex;
  flex-direction: column;
  align-self: center;
  /* border-style: dotted;
  border-width: 1px;
  border-color: blue; */
}

.chevron.inactive {
  position: relative;
  margin-top: 20px;
  background: url(/static/media/chevron.a129af26.svg);
  height: 42px;
  width: 117px;
  font-size: 12px;
  display: block;
  text-align: center;
}

.chevron.active {
  position: relative;
  margin-top: 20px;
  background: url(/static/media/chevronactive.76be97e7.svg);
  color: white;
  height: 42px;
  width: 117px;
  font-size: 12px;
  display: block;
  text-align: center;
}

.chevron>span {
  position: absolute;
  top: 50%;
  left: 50%;
  height: 30%;
  width: 50%;
  margin: -15% 0 0 -25%;
}

input {
  width: 100%;
  margin-bottom: 10px;
}



.stepButtons {
  display: block;
  width: 80%;
}


.stepButtonleft {
  float: left;
  width: 100px;
}

.stepButtonright {
  float: right;
  width: 100px;
}


