thead>tr {
  font-weight: bold;
}

.QuotationTable{
  width:100%
}

.QuotationExtra {
  text-align: right;
  font-weight: bold;
}

.QuotationPrijs {
  text-align: right;
}

.QuotationProduct {
  text-align: left;
}

.QuotationMijnPrijs {
  text-align: right;
  vertical-align: bottom;
}

.myPrice {
  width:50px;
  padding:0;
  text-align: right;
  vertical-align: bottom;
  margin:0px;
  font-size: 16px;
}

.overlay {
  z-index: 900;
  position: absolute;
  top: 80px;
  left: 0px;
  background-color: rgba(100,100,100,100);
  width: 600px;
  height: 303px;
  opacity: 0.8;
  display:flex;
  align-items: center;
  justify-content: center;
}

.lds-dual-ring {
  z-index: 910;
  display: block;
  width: 64px;
  height: 64px;
  align-self: center;
}


.lds-dual-ring:after {
  content: " ";
  display: block;
  width: 46px;
  height: 46px;
  margin: 1px;
  border-radius: 50%;
  border: 5px solid #f20253;
  border-color: #f20253 transparent #f20253 transparent;
  -webkit-animation: lds-dual-ring 1.2s linear infinite;
          animation: lds-dual-ring 1.2s linear infinite;
}
@-webkit-keyframes lds-dual-ring {
  0% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  100% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}
@keyframes lds-dual-ring {
  0% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  100% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.checkedField {
  display:flex;
  flex-direction: row;
}

.checkedField>span{
  padding-left:4px;
  padding-top:2px;
}

.termsAndConditions {
  font-size: 12px;
}

.termsAndConditionsText {
  width:90%;
  font-size: 12px;
  overflow:scroll;
  max-height: 300px;
  text-align:justify;
  padding-right:20px;
}

.checkboxAndLabel{
  display: flex;
  flex-direction: row;
}

.acceptCheckBox{
  width:-webkit-min-content;
  width:-moz-min-content;
  width:min-content;
  padding-right:5px;
  padding-top:3px;
  margin:0px;
}
.acceptedCheckBoxLabel {
  width:-webkit-min-content;
  width:-moz-min-content;
  width:min-content;
  padding-bottom: 40px;

}

.downloadContainer {
  display: flex;
  align-items: center;
  justify-content: center;
}

downloadSpinner {

  align-self:center;
}

.payButton {
  padding-top:10px;
}

.ul {
  border-bottom: 1px solid
}

ol li {
  float: left;
  margin: 0 10px;
  padding: 0 10px;
}

.attentionBalloon {
  width:117px;
  height:42px;
  background: url(/static/media/AttentionBalloon.882cab91.svg);
  font-size: 14px;
  font-weight: bold;
  text-align: center;
  position: absolute;
  z-index:900;
  left:130px;
  top: 195px;
  padding-top:5px;
  background-repeat: no-repeat;
  -webkit-transition:0.3s;
  transition:0.3s;
  opacity: 1;
}
